import React, { useState } from 'react';

const ContactForm = () => {
  // State for form inputs
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  });

  // Handle input change
  const handleChange = (e: any) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  // Handle form submission
  const handleSubmit = (e: any) => {
    e.preventDefault();
    // Normally, you'd send the form data to your backend here
    console.log('Form submitted:', formData);
    // Reset form after submission
    setFormData({
      name: '',
      email: '',
      subject: '',
      message: ''
    });
  };

  return (
    <div className="w-[80%] p-6">
      <h1 className="text-3xl font-bold mb-4 text-center">Contact Us</h1>
      <div className="text-center mb-6">
        <p className="text-lg">Phone: (123) 456-7890</p>
        <p className="text-lg">Email: info@company.com</p>
      </div>
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label htmlFor="name" className="block text-lg font-medium mb-1">Name:</label>
          <input
            type="text"
            id="name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
            className="w-full p-2 border border-gray-300"
          />
        </div>
        <div>
          <label htmlFor="email" className="block text-lg font-medium mb-1">Email:</label>
          <input
            type="email"
            id="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
            className="w-full p-2 border border-gray-300"
          />
        </div>
        <div>
          <label htmlFor="subject" className="block text-lg font-medium mb-1">Subject:</label>
          <input
            type="text"
            id="subject"
            name="subject"
            value={formData.subject}
            onChange={handleChange}
            required
            className="w-full p-2 border border-gray-300"
          />
        </div>
        <div>
          <label htmlFor="message" className="block text-lg font-medium mb-1">Message:</label>
          <textarea
            id="message"
            name="message"
            value={formData.message}
            onChange={handleChange}
            required
            className="w-full p-2 border border-gray-300 h-32"
          />
        </div>
        <button
          type="submit"
          className="w-full bg-gray-400 text-white py-2 font-medium text-lg hover:bg-gray-800"
        >
          Send
        </button>
      </form>
    </div>
  );
};

export default ContactForm;
